.popup {
  display: flex;
  position: fixed;
  background-color: rgba(0, 0, 0, 0.15);
  margin: 0;
  padding: 0;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  justify-content: center;
  align-items: center;
  color: #000;
  visibility: hidden;
  transition: all 0.5s linear;
  opacity: 0;
}
