.popup__input {
  max-width: 358px;
  width: 100%;
  min-height: 27px;
  border: none;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}

.popup__input:nth-child(2){
  margin-top: 30px;
}

@media screen and (max-width: 600px) {
  .popup__input {
    max-width: 238px;
    width: 100%;
  }
}